
// OrderDashboard.tsx
'use client';
import React, { useState } from 'react';
import AllOrders from '@/components/orders/all-orders';
import PendingOrders from '@/components/orders/';
import CompletedOrders from '@/components/orders/CompletedOrders';
import OrderHistory from '@/components/orders/OrderHistory';

const tabs = [
  { id: 'all', label: 'All Orders' },
  { id: 'pending', label: 'Pending Orders' },
  { id: 'completed', label: 'Completed Orders' },
  { id: 'history', label: 'Order History' },
];

export default function OrderDashboard() {
  const [activeTab, setActiveTab] = useState('all');

  const renderTabContent = () => {
    switch (activeTab) {
      case 'pending':
        return <PendingOrders />;
      case 'completed':
        return <CompletedOrders />;
      case 'history':
        return <OrderHistory />;
      default:
        return <AllOrders />;
    }
  };

  return (
    <div className="min-h-screen bg-gray-100">
      <div className="bg-white shadow mb-6">
        <div className="container mx-auto px-4 py-4">
          <h1 className="text-3xl font-bold text-center">Order Dashboard</h1>
        </div>
      </div>

      <div className="container mx-auto px-4">
        {/* Tab Navigation */}
        <div className="flex justify-center space-x-4 mb-6">
          {tabs.map((tab) => (
            <button
              key={tab.id}
              className={`px-4 py-2 text-sm font-medium rounded ${
                activeTab === tab.id
                  ? 'bg-blue-600 text-white'
                  : 'bg-gray-200 text-gray-700'
              }`}
              onClick={() => setActiveTab(tab.id)}
            >
              {tab.label}
            </button>
          ))}
        </div>

        {/* Tab Content */}
        <div className="bg-white rounded-lg shadow p-6">{renderTabContent()}</div>
      </div>
    </div>
  );
}
